tionizing finance (DEFI++). Bitcoin and Ether are the clear market leaders at the top of their respective games, each with their own individual merits and long history of success. DeFi is a relatively new phenomenon, a broad ecosystem of projects transforming finance. With BCP it’s now possible to easily gain balanced, risk-minimised exposure to all three asset groups. ")])]),_c('div',{staticClass:"col-md-12 "},[_c('div',{staticClass:"ummaryText"},[_vm._v(" Methodology: ")]),_c('div',{staticClass:"needText"},[_vm._v(" The PieDAO community came to the consensus that BTC, ETH, and DEFI are the most valuable sectors of the cryptocurrency industry. ")]),_c('div',{staticClass:"needText top10"},[_vm._v(" As a community we decided that the true balanced crypto portfolio would evenly spread exposure across these three asset groups. BCP is therefore composed of an even 33.33% distribution between BTC, ETH, and DEFI++. ")]),_c('div',{staticClass:"needText top10"},[_vm._v(" DEFI++ is our most diversified DEFI index, a 70/30 balance of our large market cap (DEFI+L) and small cap market cap (DEFI+S) DEFI products. Both these products follow a weight-based methodology, with higher allocation given to larger market cap assets. This is done in order to maximise the scalability of the pies, without the low liquidity of low cap (but high potential) assets becoming a bottleneck. ")])]),_c('div',{staticClass:"col-md-12 "},[_c('div',{staticClass:"ummaryText"},[_vm._v(" Operation: ")]),_c('div',{staticClass:"needText"},[_vm._v(" BCP is constructed using a Balancer smart pool, automatically rebalancing to maintain the set allocation as market prices fluctuate. In this way the system constantly locks in profits and grows the underlying holdings. ")]),_c('div',{staticClass:"needText top10"},[_vm._v(" A user can easily issue BCP tokens with ETH (sourcing the tokens from Uniswap and other markets) or with the underlying assets in their wallet. In this way BCP tokens are fully backed by the as